
  
  #progress {
    position: fixed;
    top: 20px;
    left: 20px;
    transform: rotate(-90deg);
  }
  
  circle {
    stroke-dashoffset: 0;
    stroke-width: 15%;
    fill: none;
  }
  
  .bg {
    stroke: var(--accent);
    opacity: 0.3;
  }
  
  #progress .indicator {
    stroke: var(--accent);
  }
  
  ul {
    display: flex;
    list-style: none;
    height: 300px;
    overflow-x: scroll;
    padding: 20px 0;
    flex: 0 0 600px;
    margin: 0 auto;
  }
  
  ::-webkit-scrollbar {
    height: 5px;
    width: 5px;
    background: #fff3;
    -webkit-border-radius: 1ex;
  }
  
  ::-webkit-scrollbar-thumb {
    background: var(--accent);
    -webkit-border-radius: 1ex;
  }
  
  ::-webkit-scrollbar-corner {
    background: #fff3;
  }
  
  li {
    flex: 0 0 200px;
    background: var(--accent);
    margin: 0 20px 0 0;
  }
  
  li:last-of-type {
    margin: 0;
  }
  
  .refresh {
    padding: 5px;
    position: absolute;
    border: 1px dotted var(--accent);
    border-radius: 5px;
    width: 20px;
    height: 20px;
    top: 10px;
    right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  
  .refresh path {
    fill: var(--accent);
  }
  
  .container {
    position: relative;
    width: 512px;
  }
  .box {
    width: 512px;
    height: 512px;
    border-radius: 50%;
    align-items: center;
    /* background: var(--accent); */
    background-position: center;
    background-size: 512px 512px;
    background-image: url("spinner.png");
  
    transform: rotate(40deg);
  }
  
  .arrow {
    position: absolute;
    z-index: 9;
    top: 120px;
    left: 232px;
    align-self: center;
  }
  